import { Component, OnInit, AfterViewInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import * as moment from 'moment';
import { ApiCartService } from 'src/app/shared/api-services/cart.service';
import { ProductService } from '../../../shared/services/product.service';

@Component({
  selector: 'app-success',
  templateUrl: './success.component.html',
  styleUrls: ['./success.component.scss']
})
export class SuccessComponent implements OnInit {

  public orderDetails: any = null;

  constructor(
    public productService: ProductService,
    private apiCartService: ApiCartService,
    private route: ActivatedRoute,
  ) { }

  ngOnInit(): void {	
    this.orderDetails = this.route.snapshot.data['order'];
  }

  getDateExpected() {
    return this.addBusinessDays(moment(this.orderDetails.created_at), 2).format('DD/MM/YYYY');
  }

  getOrderCreatedAt() {
    return moment(this.orderDetails.created_at).format('DD/MM/YYYY');
  }

  addBusinessDays(originalDate, numDaysToAdd) {
    const Sunday = 0;
    const Saturday = 6;
    let daysRemaining = numDaysToAdd;
  
    const newDate = originalDate.clone();
  
    while (daysRemaining > 0) {
      newDate.add(1, 'days');
      if (newDate.day() !== Sunday && newDate.day() !== Saturday) {
        daysRemaining--;
      }
    }
  
    return newDate;
  }

  getFloat(value) {
    return parseFloat(value).toFixed(2);
  }
}
